body {
  --link-color: #00acdc;
  --bg-color: #fff;
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav {
  background-color: var(--bg-color);
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list :first-child {
  margin-left: 0;
}

.nav__list :last-child {
  margin-right: 0;
}

.nav__item {
  margin: 0 10px;
  text-decoration: none;
  list-style: none;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: var(--link-color);
}

.logo {
  width: 40px;
  height: 40px;
}
